/**
 * 
 * @type {{treeFilter(*=, *): *[], combination(*): any[]}}
 */
export let mathUtil = {
  /**
   * 
   * @param list  [ ['a1','a2'], ['b1','b2']]
   * @return {any[]}  [ [ 'a1', 'b1' ], [ 'a1', 'b2' ], [ 'a2', 'b1' ], [ 'a2', 'b2' ] ]
   * 1lengthlength
   * 2list.length0list
   * 31listlist
   * 41
   * 51
   */
  combination(list){
    let pointList = []; // 
    let maxLengthList = []; // 
    let length = 1;
    // 
    for(let groupIndex = 0; groupIndex < list.length ; groupIndex ++){
      let group = list[groupIndex];
      maxLengthList.push(group.length);
      length = length * group.length;
      pointList.push(0);
    }
    console.log(`${length}`);
    /**
   * 
   */
    function updatePointList() {
      for(let i = pointList.length - 1; i > -1; i--){
        if(pointList[i] < maxLengthList[i] - 1){
          pointList[i] ++;
          break;
        }

        pointList[i] = 0;
      }
    }
    let result = [];// 
    for(let resultIndex = 0 ; resultIndex < length ; resultIndex ++){
      result[resultIndex] = [];
      // item
      for(let pointIndex = 0 ; pointIndex < pointList.length ; pointIndex ++){
        let point = pointList[pointIndex];
        result[resultIndex].push(list[pointIndex][point]);
      }
      updatePointList();

    }
    return result;
  },
  /**
   * 
   * @param tree
   * [
   {
            id: 1,
            children:[]
        }
   ]
   * @param filterCondition   id [221, 121];
   * @returns {[]}
   */
  treeFilter(tree, filterCondition) {
    function throughFilter(treePart) {
      let result = [];
      for(let item of treePart){
        if(filterCondition.indexOf(item.id) !== -1){
          result.push(item);
          continue;
        }

        if(!item.children || !item.children.length){
          continue;
        }

        let childrenResult = throughFilter(item.children);
        if(childrenResult.length > 0){
          item.children = childrenResult;
          result.push(item);
        }
      }
      return result;
    }
    return throughFilter(tree);
  },
  toThousand(data) {
    let value = data.toString();
    let trans = '';
    while (value.length > 3) {
      trans = ',' + value.slice(-3) + trans;
      value = value.slice(0, value.length - 3);
    }
    if (value) {
      return value + trans;
    }
  },
  /**
   * BigIntbaseexponent
   * @param x bigint 
   * @param y bigint 
   * @returns {bigint|*}
   */
  bigIntPow(x, y) {
    if(y === BigInt(1)){
      return BigInt(0);
    }

    let result = x;
    for(let i = BigInt(1); i < y; i++){
      result *= x;
    }
    return result;
  }
};
